@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-lavendar: #5757b8;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black:#000000;
  --primary-purple: #800080;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/**********************NAVBAR************************/

nav {
  z-index: 2;
}

.logo {
  /* width: 4.7rem; */
}

/**********************MENU LINKS************************/
nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: .75rem;
  margin-left: .75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(52, 58, 64);
  border-top: 0.1875rem solid var(--primary-dark) 
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  /* height: 6rem !important; */
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
border-top: 0.1875rem solid #5757b8;
border-top: 0.1875rem solid var(--primary-lavendar)
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #5757b8;
  border-top: 0.1875rem solid var(--primary-lavendar)
}

@media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

/**********************HEADER************************/

.header-wrapper {
  position: relative;
  background: url("/portfolio/static/media/earthbnd bkgrd.41998bca.png") no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: #5757b8;
  color: var(--primary-lavendar);
  text-transform: uppercase;
}

/********************** TYPED TEXT ************************/
.typed-text {
  font-size: 2rem;
  color: #5757b8;
  color: var(--primary-lavendar);
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

/********************** HEADER MAIN OFFER BUTTON ************************/
.btn-main-offer {
border: 1px solid #5757b8;
border: 1px solid var(--primary-lavendar);
text-transform: uppercase;
border-radius: 0;
padding: 0.625rem 0.75rem 0.75rem 0.75rem;
color: #5757b8;
color: var(--primary-lavendar);
margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #5757b8;
  background-color: var(--primary-lavendar);
  color: #ffffff;
  color: var(--primary-white);
  transition: 0.2s;
}

/********************** PARTICLES JS ************************/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display:none;
  }
}

/**********************ABOUT ME************************/
.photo-wrap {
  display:flex;
  justify-content: center;
  align-items: center;
  
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #5757b8;
  border: 1px solid var(--primary-lavendar);
}

.about-heading {
  color: #5757b8;
  color: var(--primary-lavendar);
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img {
    width:10rem;
  }
}

/**********************SERVICES************************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.services h1 {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #5757b8;
  background: var(--primary-lavendar);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.services .box {
  height: 16rem;
  border-bottom: .3125rem solid #5757b8;
  border-bottom: .3125rem solid var(--primary-lavendar);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: #5757b8;
  background: var(--primary-lavendar);
  border-bottom: .3125rem solid var(--primary-u);
}

.services .box:hover .icon {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #ffffff;
  color: var(--primary-white);
}

.services .box:hover h3 {
  color: #ffffff;
  color: var(--primary-white);
}

/**********************EXPERIENCE************************/
/* .experience {
  padding: 3rem;
} */

.experience-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #5757b8;
  color: var(--primary-lavendar);
}

.experience-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64);
  border: 0.3125rem solid var(--primary-dark);
  background: #5757b8;
  background: var(--primary-lavendar);
  margin-top: 00.625rem;
  z-index:3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #800080;
  color: var(--primary-purple);
  font-weight: 900
}

/**********************MEDIA************************/

@media(max-width: 768px) {
.experience .container{
  padding-left: 0;
}

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%
  }
  .timeline-block-left {
    float: none;
    direction:ltr;
    width: 100%
  }
}

/**********************PORTFOLIO************************/

.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #5757b8;
  color: var(--primary-lavendar)
}

.portfolio-image {
  width: 15rem;
  height: 8rem;
  border: 1px solid #5757b8;
  border: 1px solid var(--primary-lavendar);
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

.popupbox-wrapper {
  margin-top: 7rem;
}

/**********************OVERFLOW BOX************************/
.overflow {
  position:absolute;
    opacity:0;
    background: #000000;
    background: var(--primary-black);
    width:15rem;
    height: 8rem;
    top:0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.porfolio-icon {
  position: absolute;
  opacity: 1;
  margin-left: -8rem;
  margin-top: 3.5rem;
  color: #5757b8;
  color: var(--primary-lavendar);
  font-size: 1.5rem;
}

.porfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

/**********************POPUP************************/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: #5757b8;
  color: var(--primary-lavendar);
}

.hyper-link:hover {
  cursor: pointer;
  color: #ef4035;
  color: var(--primary-red);
}

/**********************MEDIA************************/
@media(max-width: 1440px) {
  .portfolio-image-popupbox {
    width: 60%;
  }
}

@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 80%;
  }
  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
  .portfolio-image-popupbox {
    width: 30%;
  }
}

/**********************TESTIMONIALS************************/
.testimonials {
  background-image: url(/portfolio/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #ffffff;
  color: var(--primary-white);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgba(0,0,0,0.7);
  background: var(--primary-transparent-black);
}

.myCarousel {
  color: #ffffff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel h3 {
  color: #5757b8;
  color: var(--primary-lavendar);
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: #ccd1d9;
  color: var(--primary-grey);
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100%;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
}

/**********************CONTACTS COMPONENT************************/
.contacts {
  background: rgb(52, 58, 64);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #5757b8;
  color: var(--primary-lavendar);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/**********************INPUTS************************/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: .125rem solid #666666;
  border-bottom: .125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #ffffff;
  color: var(--primary-white);
  border-bottom: .125rem solid #666666;
  border-bottom: .125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: .125rem;
  background: #5757b8;
  background: var(--primary-lavendar);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #ffffff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}

/**********************MEDIA************************/
@media(max-width: 768px) {
  .contacts {
    overflow-x:hidden;
  }

  .contacts .container {
    padding: 0 1;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/**********************FOOTER************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #5757b8;
  color: var(--primary-lavendar);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}
